import { ViewChild, Component, OnInit, AfterViewInit, ElementRef } from '@angular/core';

import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';

import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';

import { ClipboardService } from 'ngx-clipboard'
import { Options } from '@angular-slider/ngx-slider';
import * as moment from "moment";
@Component({
  selector: 'app-participants-sessions',
  templateUrl: './participants-sessions.component.html',
  styleUrls: ['./participants-sessions.component.css']
})
export class ParticipantsSessionsComponent implements OnInit {
  emailId
  roundData = []
  LoginForm2: FormGroup = new FormGroup({
    typee: new FormControl('', [Validators.required]),

  });
  constructor(private formBuilder: FormBuilder,
    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute,
    private _clipboardService: ClipboardService) { }

  ngOnInit(): void {
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)

      if (params.emailId) {
        this.emailId = params.emailId
        this.onRoundData()
      }
    })
  }

  onRoundData() {

    // co nsole.log(this.LoginForm.value)
    let data = {
      participentEmail: this.emailId
    }

    this.service.postApiWithAuth(`web/getParticipentSession`, data, 1).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.roundData = success.result.rows
        console.log(this.roundData)
      }
      else {


      }
    }, error => {
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })


  }

  onOptionsSelected(data) {
    console.log({ data, LoginForm: this.LoginForm2.value })
    if (this.LoginForm2.value.typee == '0' || this.LoginForm2.value.typee == 0) {
      let data = {
        participentEmail: this.emailId
      }

      this.service.postApiWithAuth(`web/getParticipentSession?sort=0`, data, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.roundData = success.result.rows
          console.log(this.roundData)
        }
        else {


        }
      }, error => {
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })
    } else if (this.LoginForm2.value.typee == '1' || this.LoginForm2.value.typee == 1) {
      this.service.postApiWithAuth(`web/getParticipentSession?sort=1`, data, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.roundData = success.result.rows
          console.log(this.roundData)
        }
        else {


        }
      }, error => {
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })
    } else {
      this.onRoundData()
    }

  }
  onClipBoard(url) {
    // ${this.service.websiteurl}user/recording-details?urlId=31
    this._clipboardService.copy(`${this.service.websiteurl}user/recording-details?urlId=` + url.id)
    this.service.succ('Url copy successfully')
  }
  onClipBoardH(url) {
    // ${this.service.websiteurl}user/highlight-details?urlId=31
    this._clipboardService.copy(`${this.service.websiteurl}user/highlight-details?urlId=` + url.id)
    this.service.succ('Url copy successfully')
  }
  getDuration(time) {
    let hours = Math.floor(time / 3600);
    let totalSeconds = time % 3600;
    let minutes = Math.floor(totalSeconds / 60);
    let seconds = Math.round(totalSeconds % 60);


    let h = hours.toString()
    let m = minutes.toString()
    let s = seconds.toString()

    let finalh

    h.length > 1 ? h : '0' + h
    let finalm = m.length > 1 ? m : '0' + m
    let finals = s.length > 1 ? s : '0' + s

    // console.log(h,m,s)
    let timerValue

    if (hours > 0) {
      timerValue = finalh + ':' + finalm + ':' + finals
    } else {
      timerValue = finalm + ':' + finals
    }

    return timerValue
  }


}
