  updateProgress = () => {

    // console.log(this.playerRef.nativeElement)
    // console.log(this.playerRef.nativeElement.duration)

    let time = Math.floor(this.playerRef.nativeElement.currentTime)
    // this.selectedTime = time
    let showInvitationArray = this.instructions.filter(item => { return item.time == time })
    for (let index = 0; index < showInvitationArray.length; index++) {
      const element = showInvitationArray[index];

      if (this.showInvitationArray.length == 0) {
        this.showInvitationArray.push(element)
      } else {
        let localIndex = this.showInvitationArray.map(it => { return it.id }).indexOf(element.id)

        if (localIndex == -1) {
          this.showInvitationArray.push(element)
        }

      }

      // console.log(this.showInvitationArray)
      setTimeout(function () {
        $('.idclass' + element.id).fadeOut('slow');
      }, 3000);
    }

    // this.progressWidth = `${this.playerRef.nativeElement.currentTime / (this.roundData.duration - 1) * 100}%`;
    this.value=this.playerRef.nativeElement.currentTime
    this.progressValue = Math.round(
      (this.playerRef.nativeElement.currentTime / (this.roundData.duration - 1)) * 100
    );
    this.currentTime = this.playerRef.nativeElement.currentTime
    // console.log(this.playerRef.nativeElement.currentTime, this.roundData.duration - 1)

    if (Math.round(this.playerRef.nativeElement.currentTime) == (this.roundData.duration - 1)) {

    }
  }
