  getMinuteHours(time) {
    let hours = Math.floor(time / 3600);
    let totalSeconds = time % 3600;
    let minutes = Math.floor(totalSeconds / 60);
    let seconds = Math.round(totalSeconds % 60);


    let h = hours.toString()
    let m = minutes.toString()
    let s = seconds.toString()

    let finalh

    h.length > 1 ? h : '0' + h
    let finalm = m.length > 1 ? m : '0' + m
    let finals = s.length > 1 ? s : '0' + s

    // console.log(h,m,s)
    let timerValue

    if (hours > 0) {
      timerValue = finalh + ':' + finalm + ':' + finals
    } else {
      timerValue = finalm + ':' + finals
    }

    return timerValue
  }

