import { Component, OnInit } from '@angular/core';


import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';

import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
import { resourceUsage } from 'process';
@Component({
  selector: 'app-project-edit',
  templateUrl: './project-edit.component.html',
  styleUrls: ['./project-edit.component.css']
})
export class ProjectEditComponent implements OnInit {


  projectFrom: FormGroup
  loginUserData
  projectId
  formInvalid = false

  constructor(
    private formBuilder: FormBuilder,
    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {

    this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)
      this.projectId = params.projectId
    })
    console.log(this.loginUserData)

    this.projectFrom = this.formBuilder.group({
      // orgId: new FormControl('', [Validators.required]),
      title: [null, Validators.required],
      description: [null],

      // instructions:this.formBuilder.array([this.createItem()])
    });
this.onGetData()
  }

  onDeleteApi(){
    let apiData = {
      "projectId": this.projectId
    }

    this.service.postApiWithAuth('web/deleteProject', apiData, 1).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
       
        this.service.succ("Project Deleted")
        this.route.navigate(['/user','project-list'])

      }
      else {
        this.service.err(success.message)
        // this.service.sweetAlert(success.message)
        this.service.err(success.message)

      }
    }, error => {
      this.service.err("Something went wrong")
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })
  }
  onGetData() {
    let apiData = {
      "projectId": this.projectId
    }

    this.service.postApiWithAuth('web/findProjectDetails', apiData, 1).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.projectFrom.patchValue({
          title: success.result.title,
          description:  success.result.description,
         

        });

        // this.service.succ("Project Saved")

      }
      else {
        this.service.err(success.message)
        // this.service.sweetAlert(success.message)
        this.service.err(success.message)

      }
    }, error => {
      this.service.err("Something went wrong")
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })
  }
  onSubmit() {

    if (this.projectFrom.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      return

    } else {
      this.formInvalid = false
      console.log(this.projectFrom.value)
      this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
      console.log(this.loginUserData)

      let apiData = {
        "title": this.projectFrom.value.title,
        "description": this.projectFrom.value.description,
        "projectId":this.projectId
      }

      this.service.postApiWithAuth('web/updateProject', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {


          this.service.succ("Project Saved")

        }
        else {
          this.service.err(success.message)
          // this.service.sweetAlert(success.message)
          this.service.err(success.message)

        }
      }, error => {
        this.service.err("Something went wrong")
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })
      this.service.showSpinner()
    }
  }

}
