  onSaveCropFile() {
    let fileNameData = this.roundData.seeionUrl.split('/')
    let fileName = fileNameData[fileNameData.length - 1]
    // get total seconds between the times
    var delta = this.minSelected;

    // calculate (and subtract) whole days
    var days = Math.floor(delta / 86400);
    delta -= days * 86400;

    // calculate (and subtract) whole hours
    var hours = Math.floor(delta / 3600) % 24;
    delta -= hours * 3600;

    // calculate (and subtract) whole minutes
    var minutes = Math.floor(delta / 60) % 60;
    delta -= minutes * 60;

    // what's left is seconds
    var seconds = delta % 60;

    console.log({ hours, minutes, seconds })

    let finalString
    if (hours < 10) {
      finalString = '0' + hours
    } else {
      finalString = hours
    }

    if (minutes < 10) {
      finalString = finalString + ':' + '0' + minutes
    } else {
      finalString = finalString + ':' + minutes
    }
    if (seconds < 10) {
      finalString = finalString + ':' + '0' + seconds
    } else {
      finalString = finalString + ':' + seconds
    }

    let formData = new FormData();
    formData.append('end_time', this.maxSelected.toString());
    formData.append('start_time', this.minSelected.toString());
    formData.append('input_video', '/var/www/html/UserTesting/' + fileName);
    let reqData = {
      start_time: this.minSelected,
      end_time: this.maxSelected,
      input_video: '/var/www/html/UserTesting/' + fileName


    }

    console.log(formData)
    this.service.formdataApiCrop(`crop-video/`, formData).subscribe((success) => {
      console.log(success)
      if (success.status == 200) {
        this.onsaveHighLightsWithoutCrop(success.output_video_name)
        // this.service.succ("Hightlights saved successfully")
      }
      else {
        this.service.err(success.message)
      }
    }, error => {
      console.log(error)
      // this.service.sweetAlert("Something went wrong")
      this.service.err("Something went wrong")

    })
  }

