  onIndexChange(type) {

    if (this.selectedIndex < this.projectFrom.value.instructions.length - 1 && type == 'add') {
      this.selectedIndex = this.selectedIndex + 1
    }

    if (this.selectedIndex > 0 && type == 'min') {
      this.selectedIndex = this.selectedIndex - 1
    }
  }
  onSubmit() {
    let data = []
    this.instaructionError = false
    for (let index = 0; index < this.projectFrom.value.instructions.length; index++) {
      const element = this.projectFrom.value.instructions[index];

      if (element.instruct) {
        data.push({ instruct: element.instruct })
      }


    }
    if (this.projectFrom.invalid || data.length == 0) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      if (data.length == 0) {
        this.instaructionError = true
      }
      debugger
      return

    } else {
      this.formInvalid = false
      console.log(this.projectFrom.value)
      this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
      console.log(this.loginUserData)


      let apiData = {
        "welcomeMessage": this.projectFrom.value.finalMessage,
        "landingPage": this.projectFrom.value.landingPage,
        "instruction": JSON.stringify(data),
        "finalMessage": this.projectFrom.value.finalMessage,
        "projectId": this.projectId
      }

      if (this.roundId) {
        apiData['roundId'] = this.roundId
        this.service.postApiWithAuth('web/updateRound', apiData, 1).subscribe((success) => {
          console.log(success)
          if (success.response == true) {
            this.service.succ(success.message)
          }
          else {
            this.service.err(success.message)
          }
        }, error => {
          this.service.err("Something went wrong")
        })

      } else {
        this.service.postApiWithAuth('web/createRound', apiData, 1).subscribe((success) => {
          console.log(success)
          if (success.response == true) {
            this.service.succ(success.message)
            this.roundId = success.result.id
          }
          else {
            this.service.err(success.message)
          }
        }, error => {
          this.service.err("Something went wrong")
        })
      }

      this.service.showSpinner()
    }
  }

