  togglePlay() {
    debugger
    if (this.video.paused) {
      this.video.play();
    } else {
      this.video.pause();
    }
    this.playBtn.classList.toggle('paused');
  }

  onPlay() {
    if (this.video.paused) {
      this.video.play();
    } else {
      this.video.pause();
    }
    this.playBtn.classList.toggle('paused');
  }

  togglePlayBtn() {
    this.playBtn.classList.toggle('playing');
  }

  toggleMute() {
    if (this.video.volume) {
      this.lastVolume = this.video.volume;
      this.video.volume = 0;
      this.volumeBtn.classList.add('muted');
      this.volumeFill.style.width = 0;
    } else {
      this.video.volume = this.lastVolume;
      this.volumeBtn.classList.remove('muted');
      this.volumeFill.style.width = `${this.lastVolume * 100}%`;
    }
  }

  changeVolume(e) {
    this.volumeBtn.classList.remove('muted');

    let volume = e.offsetX / this.volumeSlider.offsetWidth;
    volume < 0.1 ? volume = 0 : volume = volume;
    this.volumeFill.style.width = `${volume * 100}%`;
    this.video.volume = volume;
    if (volume > 0.7) {
      this.volumeBtn.classList.add('loud');
    } else if (volume < 0.7 && volume > 0) {
      this.volumeBtn.classList.remove('loud');
    } else if (volume == 0) {
      this.volumeBtn.classList.add('muted');
    }
    this.lastVolume = volume;
  }
  neatTime(time) {
    // var hours = Math.floor((time % 86400)/3600)
    var minutes = Math.floor((time % 3600) / 60);
    var seconds = Math.floor(time % 60);
    seconds = seconds > 9 ? seconds : seconds;
    return `${minutes}:${seconds}`;
  }

  updateProgress = () => {

    console.log(this.videotag.nativeElement.currentTime)
    console.log(Math.floor(this.videotag.nativeElement.currentTime))
    let time = Math.floor(this.videotag.nativeElement.currentTime)
    this.selectedTime = time
    let showInvitationArray = this.instructions.filter(item => { return (item.time == time) && item.show == false })
    console.log(showInvitationArray)

    for (let index = 0; index < showInvitationArray.length; index++) {
      const element = showInvitationArray[index];

      if (this.showInvitationArray.length == 0) {
        this.showInvitationArray.push(element)
      } else {
        let localIndex = this.showInvitationArray.map(it => { return it.id }).indexOf(element.id)

        if (localIndex == -1) {
          this.showInvitationArray.push(element)
        }

      }

      console.log(this.showInvitationArray)
      setTimeout(function () {
        $('.idclass' + element.id).fadeOut('slow');
      }, 3000);
    }
    // this.progressFill.style.width = `${this.video.currentTime / this.video.duration * 100}%`;
    this.progressWidth = `${this.videotag.nativeElement.currentTime / (this.roundData.duration - 1) * 100}%`;
    this.textCurrent.innerHTML = `${this.neatTime(this.videotag.nativeElement.currentTime)} / ${this.neatTime((this.roundData.duration - 1))}`;


    if (Math.round(this.videotag.nativeElement.currentTime) == (this.roundData.duration - 1)) {
      this.playBtn.classList.toggle('paused');
    }
    // textTotal.innerHTML = neatTime(video.duration);
    // console.log(this.progressFill.style.width);
  }

