import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';

import { Router, ActivatedRoute } from '@angular/router';
import { StripeService, StripeCardComponent } from 'ngx-stripe';
import {
  StripeCardElementOptions,
  StripeElementsOptions
} from '@stripe/stripe-js';

@Component({
  selector: 'app-payment-gatway',
  templateUrl: './payment-gatway.component.html',
  styleUrls: ['./payment-gatway.component.scss']
})
export class PaymentGatwayComponent implements OnInit {
  @ViewChild(StripeCardComponent) card: StripeCardComponent;

  cardOptions: StripeCardElementOptions = {
    style: {
      base: {
        iconColor: '#666EE8',
        color: '#31325F',
        fontWeight: '300',
        fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
        fontSize: '18px',
        '::placeholder': {
          color: '#CFD7E0'
        }
      }
    }
  };

  elementsOptions: StripeElementsOptions = {
    locale: 'en'
  };
  projectFrom: FormGroup
  formInvalid = false
  type = 'password'
  loginUserData = null
  constructor(
    private route: Router,
    private service: AppService,
    private stripeService: StripeService
  ) { }


  // createdBy: { type: Sequelize.INTEGER},
  // orgId: { type: Sequelize.INTEGER},
  // title: { type: Sequelize.STRING },
  // description: { type: Sequelize.STRING},
  ngOnInit(): void {
    this.projectFrom = new FormGroup({
      // orgId: new FormControl('', [Validators.required]),
      creditcount: new FormControl('', [Validators.required]),
      name: new FormControl('', [Validators.required]),

    });
  }

  createToken(): void {
    const name = this.projectFrom.get('name').value;

    if (this.projectFrom.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      return

    } else {
      console.log('hy')
      this.stripeService
        .createToken(this.card.element, { name })
        .subscribe((result) => {
          if (result.token) {
            // Use the tokenon
            console.log(result.token)
            console.log(result.token.id);
            this.onSubmit(result.token)
          } else if (result.error) {
            // Error creating the token
            console.log(result.error.message);
          }
        });
    }
  }
  onSubmit(token) {

    if (this.projectFrom.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')
      return
    } else {
      this.formInvalid = false
      console.log(this.projectFrom.value)
      this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
      console.log(this.loginUserData)
      let apiData = {
        "email": this.loginUserData.email,
        "stripeTokenId": token.id,
        "stripeToken": token,
        "orgId": this.loginUserData.orgDetails.id,
        "userId": this.loginUserData.id,
        "name": this.projectFrom.get('name').value,
        "payAmount": (this.projectFrom.get('creditcount').value * 59 * 100),
        "creditcount": this.projectFrom.get('creditcount').value

      }

      this.service.postApiWithAuth('web/payAmount', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.service.succ('Payment successfull')

          this.route.navigate(['/user', `project-list`], { queryParams: { projectId: success.result.id, newProject: 'yes' } })



          //  this.service.sweetAlert('User Registration Successfull')

        }
        else {
          this.service.err(success.message)
          // this.service.sweetAlert(success.message)
          // this.service.err(success.message)

        }
      }, error => {
        this.service.err("Something went wrong")
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })
      this.service.showSpinner()
    }
  }

}
