import { Component, OnInit } from '@angular/core';

import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Options } from '@angular-slider/ngx-slider';
declare var $;
@Component({
  selector: 'app-user-interview',
  templateUrl: './user-interview.component.html',
  styleUrls: ['./user-interview.component.css']
})
export class UserInterviewComponent implements OnInit {
  LoginForm: FormGroup
  formData = new FormData();
  formInvalid = false
  submitted = false
  invitationId
  orgId
  orgName
  source
  type = 'password'
  projectId
  roundId
  priceList
  creditData
  totalAvailbaleCredit: 0
  deleteFlag = false

  totalCredit = 0
  showGenrateUrl = false
  totalCreditReq



  errors = []
  // sliderForm: FormGroup = new FormGroup({
  //   sliderControl: new FormControl([20, 80])
  // });
  ageOptions: Options = {
    floor: 16,
    ceil: 100,
    step: 2
  };


  houseOptions: Options = {
    floor: 0,
    ceil: 1000000,
    step: 1000
  };

  constructor(
    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) {

  }


  ngOnInit(): void {
    this.getGetPriceList()
    this.getCreditData()
    $(".Button").click(function () {
      $(".modal-backdrop").removeClass("modal-backdrop fade in");
      $("body").removeClass("modal-open");
      $("body").addClass("padding_none");

    });
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)
      this.projectId = params.projectId
      if (params.roundId) {
        this.roundId = params.roundId

      }
    })
    this.LoginForm = new FormGroup({
      participantCount: new FormControl('', [Validators.required]),
      deviceType: new FormControl('', [Validators.required]),
      ageGroup: new FormControl([16, 80]),
      houseHold: new FormControl([0, 80000]),
      gender: new FormControl('', [Validators.required]),
      webExperties: new FormControl('', [Validators.required]),
      requirment: new FormControl('', [Validators.required]),
      urlType: new FormControl('', [Validators.required]),
    });
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)
      if (params && params.invitationId) {
        this.invitationId = params.invitationId
      }

      if (params && params.orgId) {
        this.orgId = params.orgId
      }

      if (params && params.orgName) {
        this.orgName = params.orgName
      }

      if (params && params.source) {
        this.source = params.source
      }

      if (params && params.email) {
        this.LoginForm.patchValue({
          email: params.email

        });
      }

      if (params && params.orgName) {
        this.LoginForm.patchValue({
          organization: params.orgName

        });
      }

    });
  }


  get f() {
    return this.LoginForm.controls;
  }


  getCreditData() {


    this.service.getApiWithAuth('web/creditList').subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.creditData = success.result
        if (success.result) {
          this.totalCredit = success.result.credit
        }

      }
      else {
        this.service.err(success.message)
        this.service.err(success.message)

      }
    }, error => {
      this.service.err("Something went wrong")


    })
  }
  getGetPriceList() {

    this.service.getApi('web/findAllPrice').subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.priceList = success.result.filter(it => it.type == '1')
      }
      else {
        this.service.err(success.message)


      }
    }, error => {
      this.service.err("Something went wrong")
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })
    this.service.showSpinner()
  }

  imageUploadEvent(evt: any) {
    if (!evt.target) {
      return;
    }
    if (!evt.target.files) {
      return;
    }
    if (evt.target.files.length !== 1) {
      return;
    }
    const file = evt.target.files[0];

    console.log(file)
    // if (file.type !== 'image/jpeg' && file.type !== 'image/png' && file.type !== 'image/gif' && file.type !== 'image/jpg') {
    //   return;
    // }

    let fileToUpload = file;
    this.formData.append('requested', fileToUpload, fileToUpload.name);

    console.log(this.formData)
    // const fr = new FileReader();
    // fr.onloadend = (loadEvent) => {
    //   console.log(fr.result)
    //   ///this.userDetail.image= fr.result;
    //   // console.log(this.userDetail.image)
    // };
    // fr.readAsDataURL(file);
  }

  onSubmit() {


    if (!this.LoginForm.valid) {
      this.formInvalid = true
      this.service.err("Please fill all required data")
      return
    }
    this.formInvalid = false
    let priceData = this.priceList.filter(it => it.id == this.LoginForm.value.urlType)

    if (priceData.length > 0) {

      let count = parseInt(this.LoginForm.value.participantCount)
      let creditReq = priceData[0].creditRequired
      this.totalCreditReq = creditReq * count

      debugger
      if (this.totalCredit >= this.totalCreditReq) {
        this.showGenrateUrl = true
      } else {
        this.showGenrateUrl = false
      }


    } else {
      this.showGenrateUrl = false
    }
    if (this.showGenrateUrl) {


      console.log(this.LoginForm.value.urlType)
      this.formData.append('participantCount', this.LoginForm.value.participantCount);
      this.formData.append('deviceType', this.LoginForm.value.deviceType);
      this.formData.append('ageGroup', this.LoginForm.value.ageGroup);
      this.formData.append('houseHold', this.LoginForm.value.houseHold);
      this.formData.append('gender', this.LoginForm.value.gender);
      this.formData.append('webExperties', this.LoginForm.value.webExperties);
      this.formData.append('requirment', this.LoginForm.value.requirment);
      this.formData.append('projectId', this.projectId);
      this.formData.append('roundId', this.roundId);
      this.formData.append('reqDurationAllow', priceData[0].usesMint);
      this.formData.append('totalCreditReq', this.totalCreditReq);
      this.formData.append('urlType', this.LoginForm.value.urlType);


      this.service.formdataApi(`web/userInterViewRequest`, this.formData).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.LoginForm.reset()
          this.formData = new FormData();
          this.getCreditData()
          $('#ConfirmModal').modal('show')
          //  this.service.succ(success.message)
          //this.onroundList()

          //this.linkUrl = success.result

        }
        else {
          this.service.err(success.message)

        }
      }, error => {
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })

    } else {
      this.service.err('Please check your credit balance')
    }


  }
}
