import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AppProvider } from '../../app.provider'
declare var $
@Component({
  selector: 'app-myaccount',
  templateUrl: './myaccount.component.html',
  styleUrls: ['./myaccount.component.css']
})
export class MyaccountComponent implements OnInit {
  LoginForm: FormGroup
  password: FormGroup
  formInvalid = false
  submitted = false
  invitationId
  orgId
  orgName
  source
  type = 'password'
  loginUserData
  constructor(
    private appProvider: AppProvider,
    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) {

  }


  ngOnInit(): void {
    this.LoginForm = new FormGroup({
      fullName: new FormControl('', [Validators.required, Validators.minLength(10), Validators.maxLength(30), Validators.pattern(/^[a-zA-Z ]*$/)]),
      organization: new FormControl('', [Validators.required, Validators.minLength(5), Validators.maxLength(30)]),
      password: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required, Validators.pattern('^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$')]),
    });

    this.password = new FormGroup({
      oldPass: new FormControl('', [Validators.required, Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)]),
      password: new FormControl('', [Validators.required, Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)]),
      conPass: new FormControl('', [Validators.required, Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)]),
    });
    this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))

    console.log(this.loginUserData)
    this.getUserData(false)
  }

  getUserData(staus) {

    // console.log(this.LoginForm.value)
    let apiData = {
      userId: this.loginUserData.id
    }

    this.service.getApi('web/getUserDetails?userId=' + this.loginUserData.id).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.appProvider.current.adminData = success.result
        localStorage.setItem('userTesting', JSON.stringify(success.result))
        if (staus) {
          this.route.navigate(['/user/project-list'])
        }
        this.LoginForm.patchValue(
          {
            fullName: success.result.fullName,
            organization: success.result.orgDetails.name,
            password: success.result.plainpassword,
            email: success.result.email
          }
        )

      }
      else {

        this.service.err(success.message)

      }
    }, error => {
      // this.service.sweetAlert("Something went wrong")
      this.service.err("Something went wrong")

    })


  }


  onChange() {
    this.type = this.type == 'password' ? 'text' : 'password'
  }

  get f() {
    return this.LoginForm.controls;
  }

  get g() {
    return this.password.controls;
  }

  onSubmit(value) {
    this.submitted = true

    if (this.LoginForm.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      return

    } else {
      this.formInvalid = false
      console.log(this.LoginForm.value)
      let apiData = {
        "fullName": this.LoginForm.value.fullName,
        "email": this.LoginForm.value.email,
      }





      this.service.postApiWithAuth('web/updateUser', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {



          this.service.succ(success.message)

          this.getUserData(true)


          //  this.service.sweetAlert('User Registration Successfull')

        }
        else {
          // this.service.sweetAlert(success.message)
          this.service.err(success.message)

        }
      }, error => {
        // this.service.sweetAlert("Something went wrong")
        this.service.err("Something went wrong")

      })
      this.service.showSpinner()
    }
  }
  closedModal(){
    this.password.reset()
    this.password.controls.oldPass.setErrors(null);
    this.password.controls.password.setErrors(null);
    this.password.controls.conPass.setErrors(null);
  }
  onSubmitData() {
    this.submitted = true

    if (this.password.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      return

    } else {
      this.formInvalid = false
      console.log(this.password.value)
      let apiData = {
        "passold": this.password.value.oldPass,
        "password": this.password.value.password,
      }





      this.service.postApiWithAuth('web/changePass', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {



          this.service.succ(success.message)

          this.getUserData(false)
          this.password.reset()
          this.password.controls.oldPass.setErrors(null);
          this.password.controls.password.setErrors(null);
          this.password.controls.conPass.setErrors(null);
          $('#ChangeModal').modal('hide')
          //  this.service.sweetAlert('User Registration Successfull')

        }
        else {
          // this.service.sweetAlert(success.message)
          this.service.err(success.message)

        }
      }, error => {
        // this.service.sweetAlert("Something went wrong")
        this.service.err("Something went wrong")

      })
      this.service.showSpinner()
    }

  }

}
