  onsaveHighLightsWithoutCrop(outputFile) {
    let fileNameData = this.roundData.seeionUrl.split('/')
    let fileName = fileNameData[fileNameData.length - 1]
    // get total seconds between the times
    var delta = this.minSelected;

    // calculate (and subtract) whole days
    var days = Math.floor(delta / 86400);
    delta -= days * 86400;

    // calculate (and subtract) whole hours
    var hours = Math.floor(delta / 3600) % 24;
    delta -= hours * 3600;

    // calculate (and subtract) whole minutes
    var minutes = Math.floor(delta / 60) % 60;
    delta -= minutes * 60;

    // what's left is seconds
    var seconds = delta % 60;

    console.log({ hours, minutes, seconds })

    let finalString
    if (hours < 10) {
      finalString = '0' + hours
    } else {
      finalString = hours
    }

    if (minutes < 10) {
      finalString = finalString + ':' + '0' + minutes
    } else {
      finalString = finalString + ':' + minutes
    }
    if (seconds < 10) {
      finalString = finalString + ':' + '0' + seconds
    } else {
      finalString = finalString + ':' + seconds
    }
    let reqData = {
      browser: this.roundData.browser,
      browserVersion: this.roundData.browserVersion,
      createdBy: this.roundData.createdBy,
      duration: this.maxSelected - this.minSelected,
      genratedUrlId: this.roundData.id,
      instructions: this.roundData.instructions,
      interviewId: this.roundData.interviewId,
      orgId: this.roundData.orgId,
      os: this.roundData.os,
      osVersion: this.roundData.osVersion,
      participentEmail: this.roundData.participentEmail,
      participentName: this.roundData.participentName,
      projectId: this.roundData.projectId,
      reqDurationAllow: this.roundData.reqDurationAllow,
      roundId: this.roundData.roundId,
      seeionUrl: outputFile,// "http://3.6.100.194/UserTesting/1604908690356RecordRTC-2020109-397tqjk0b2b.mp4"
      status: this.roundData.status,
      type: this.roundData.type,
      url: this.roundData.url,
      useStatus: this.roundData.useStatus,
      minSelected: this.minSelected,
      maxSelected: this.maxSelected,
      fileName: fileName,
      title: this.highLightFrom.value.title,
      extention: fileName.split('.').pop(),
      startTime: finalString
    }

    console.log(reqData)
    this.service.postApiWithAuth(`web/saveHighLightsWithoutCrop`, reqData, 1).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        this.boxCount = 4
        this.cropedDeatils = success.result
        this.service.succ("Hightlights saved successfully")
      }
      else {
        this.service.err(success.message)
      }
    }, error => {

      this.service.err("Something went wrong")

    })
  }

