  onRoundData() {



    this.service.getApiWithAuth(`web/getURLdetails?urlId=${this.projectId}`).subscribe((success) => {
      console.log(success)
      if (success.response == true) {

        this.roundData = success.result
        this.getsubscribe()
        const newOptions: Options = Object.assign({}, this.options);
        newOptions.ceil = success.result.duration ? success.result.duration : 90;
        newOptions.maxRange = success.result.duration ? success.result.duration : 90 - 10;
        this.options = newOptions;

        const newOptionsTwo: Options = Object.assign({}, this.optionsTwo);
        newOptionsTwo.ceil = success.result.duration ? (success.result.duration-1) : 90;
        this.optionsTwo = newOptionsTwo;
        // this.selectedTime = success.result.duration
        this.DescriptionForm.patchValue({
          description: this.roundData.description,
          title: this.roundData.title
        });
        if (success.result.instruction) {
          let instructions = success.result.instruction
          let instructionsList=[]

          for (let index = 0; index < instructions.length; index++) {
            const element = instructions[index];
            
            if(instructionsList.length==0){
              instructionsList.push({
                time:element.time,
                instructions:[element]
              })
            }else{

              let indexValue=instructionsList.map(it=>{return it.time}).indexOf(element.time)
              if(indexValue!=-1){
                instructionsList[indexValue].instructions.push(element)
              }else{
                instructionsList.push({
                  time:element.time,
                  instructions:[element]
                })
              }

            }
            
            
          }
          this.showInvitationArrayTime=this.sortArray(instructionsList)
          this.instructions = instructions.map(it => {
            it.show = false
            return it
          })
          console.log(this.showInvitationArrayTime)
        }

      }
      else {


      }
    }, error => {


    })


  }
