  onAdd() {

    console.log(this.LoginForm2.value)
    let values = this.LoginForm2.value

    console.log(values)

    let emails = values.emails.split(',')
    let local = []
    console.log(emails)
    if (!values.emails) {
      this.service.err("Please enter email ids")
      return
    }
    if (emails.length == 0) {
      this.service.err("Please enter email ids")
      return
    } else {
      let reg = /^[a-zA-Z]+([.-]?[a-zA-Z0-9]+)*@([a-zA-Z]+([.-]?[a-zA-Z]))[.]{1}[a-zA-Z]{2,}$/

      for (let index = 0; index < emails.length; index++) {

        if (reg.test(emails[index].trim())) {
          local.push({
            type: values.typee,
            owner: this.loginUserData.fullName,
            organisation: this.loginUserData.orgDetails.name,
            organisationId: this.loginUserData.orgDetails.id,
            email: emails[index].trim()
          })
        } else {

          local = []
          this.service.err("Please enter valid email ids")
          return
        }
        if (local.length == emails.length) {
          this.LoginForm2.
            patchValue({
              emails: ''
            });

        }
      }

      for (let index = 0; index < local.length; index++) {
        this.invitationArray.push({
          type: local[index].type,
          owner: local[index].owner,
          organisation: local[index].organisation,
          organisationId: local[index].organisationId,
          email: local[index].email.trim(),
          new: true
        })
      }
      this.onInvite()
    }
  }

