  getDuration(item) {
    var delta = item

    // calculate (and subtract) whole days
    var days = Math.floor(delta / 86400);
    delta -= days * 86400;

    // calculate (and subtract) whole hours
    var hours = Math.floor(delta / 3600) % 24;
    delta -= hours * 3600;

    // calculate (and subtract) whole minutes
    var minutes = Math.floor(delta / 60) % 60;
    delta -= minutes * 60;

    // what's left is seconds
    var seconds = delta % 60;

    console.log({ hours, minutes, seconds })

    let finalString
    if (hours < 10) {
      finalString = '0' + hours
    } else {
      finalString = hours
    }

    if (minutes < 10) {
      finalString = finalString + ':' + '0' + minutes
    } else {
      finalString = finalString + ':' + minutes
    }
    if (seconds < 10) {
      finalString = finalString + ':' + '0' + seconds
    } else {
      finalString = finalString + ':' + seconds
    }
    return finalString
  }

  onAddNote() {
    if (!this.NoteForm.value.note) {
      this.formInvalid = true
      this.service.err('Please enter note')

      return

    } else {
      this.formInvalid = false
      let apiData = {
        text: this.NoteForm.value.note,
        time: Math.floor(this.playerRef.nativeElement.currentTime),
        showTime: this.getDuration(Math.floor(this.playerRef.nativeElement.currentTime)),
        type: '1',
        sessionId: this.roundData.id,
        tagList: this.tagList,
        title: this.roundData.title,
        os: this.deviceInfo.os,
        loginuserName: this.loginUserData.fullName,
        currentTime: moment().format('LT'),

        url: `${this.service.websiteurl}user/recording-detailss?urlId=` + this.roundData.id

      }

      this.service.postApiWithAuth('web/createNote', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {
          this.selectedTime = 0
          this.NoteForm.reset()
          success.result.userInfo = {}
          success.result.userInfo.fullName = this.loginUserData.fullName
          this.instructions.push(success.result)
          this.service.succ(success.message)
          let instructions = this.instructions
          let instructionsList=[]

          for (let index = 0; index < instructions.length; index++) {
            const element = instructions[index];
            
            if(instructionsList.length==0){
              instructionsList.push({
                time:element.time,
                instructions:[element]
              })
            }else{

              let indexValue=instructionsList.map(it=>{return it.time}).indexOf(element.time)
              if(indexValue!=-1){
                instructionsList[indexValue].instructions.push(element)
              }else{
                instructionsList.push({
                  time:element.time,
                  instructions:[element]
                })
              }

            }
            
            
          }
          this.showInvitationArrayTime=this.sortArray(instructionsList)

          console.log( this.showInvitationArrayTime)
        }
        else {

          this.service.err(success.message)

        }
      }, error => {
        console.log(error)
        this.service.sweetAlert("Something went wrong")


      })

    }
  }

