  onSubmitCount() {

    console.log(this.urlCountForm.value)
    if (this.urlCountForm.invalid) {
      this.formInvalid2 = true
      // this.service.sweetAlert('fghjk')

      return

    } else {


      let priceData = this.priceList.filter(it => it.type == '0')

      if (priceData.length > 0) {

        let count = parseInt(this.urlCountForm.value.count)
        let creditReq = priceData[0].creditRequired
        this.totalCreditReq = creditReq * count

        debugger
        if (this.totalCredit >= this.totalCreditReq) {
          this.showGenrateUrl = true
          this.text = `${this.totalCreditReq} credit will be deducted from your account `
        } else {
          this.showGenrateUrl = false
          this.text = `You don’t have enough credits in your account.  <br>
          Please buy more credits.`
        }


      } else {
        this.text = `You don’t have enough credits in your account.  <br>
        Please buy more credits.`
        this.showGenrateUrl = false
      }
      $("#participantsModal").modal('hide');
      $("#BuyModal").modal('show');

    }

  }

