import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { VerificationComponent } from './verification/verification.component';
import { InviteComponent } from './invite/invite.component';
import { OrganizationSettingComponent } from './organization-setting/organization-setting.component';
import { OrganizationMembersComponent } from './organization-members/organization-members.component';
import { OrganizationBillingComponent } from './organization-billing/organization-billing.component';
import { ProjectListComponent } from './project-list/project-list.component';
import { RecordingListComponent } from './recording-list/recording-list.component';
import { ParticipantsListComponent } from './participants-list/participants-list.component';
import { HighlightsListComponent } from './highlights-list/highlights-list.component';
import { ProjectEditComponent } from './project-edit/project-edit.component';
import { ProjectSelftestComponent } from './project-selftest/project-selftest.component';
import { ProjectCreateComponent } from './project-create/project-create.component';
import { ProjectCreateRoundComponent } from './project-create-round/project-create-round.component';
import { ProjectCreateDetailsComponent } from './project-create-details/project-create-details.component';
import { RecordingDetailsComponent } from './recording-details/recording-details.component';
import { ParticipantsSessionsComponent } from './participants-sessions/participants-sessions.component';
import { MyaccountComponent } from './myaccount/myaccount.component';
import { UserInterviewComponent } from './user-interview/user-interview.component';
import { RecordingWelcomeComponent } from './recording-welcome/recording-welcome.component';
import { RecordingStartComponent } from './recording-start/recording-start.component';
import { PaymentGatwayComponent } from './payment-gatway/payment-gatway.component';
import { HighlightsDetailsComponent } from './highlights-details/highlights-details.component';
import { CreditComponent } from './credit/credit.component';
import { RecordingDetailssComponent } from './recording-detailss/recording-detailss.component';
import { HighlightsDetailssComponent } from './highlights-detailss/highlights-detailss.component';

const routes: Routes = [

  { path: 'verification', component: VerificationComponent },
  { path: 'invite', component: InviteComponent },
  { path: 'Organization-setting', component: OrganizationSettingComponent },
  { path: 'Organization-member', component: OrganizationMembersComponent },
  { path: 'Organization-billing', component: OrganizationBillingComponent },
  { path: 'project-list', component: ProjectListComponent },
  { path: 'recording-list', component: RecordingListComponent },
  { path: 'participants-list', component: ParticipantsListComponent },
  { path: 'highlights-list', component: HighlightsListComponent },
  { path: 'project-edit', component: ProjectEditComponent },
  { path: 'project-selftest', component: ProjectSelftestComponent },
  { path: 'project-create', component: ProjectCreateComponent },
  { path: 'project-create-round', component: ProjectCreateRoundComponent },
  { path: 'project-create-details', component: ProjectCreateDetailsComponent },
  { path: 'recording-details', component: RecordingDetailsComponent },
  { path: 'participants-session', component: ParticipantsSessionsComponent },
  { path: 'myaccount', component: MyaccountComponent },
  { path: 'user-interview', component: UserInterviewComponent },
  { path: 'recording-welcome', component: RecordingWelcomeComponent },
  { path: 'recording-start', component: RecordingStartComponent },
  { path: 'payment-gatway', component: PaymentGatwayComponent },
  { path: 'highlight-details', component: HighlightsDetailsComponent },
  { path: 'highlight-detailss', component: HighlightsDetailssComponent },
  { path: 'Credit', component: CreditComponent },
  { path: 'recording-detailss', component: RecordingDetailssComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoutingModule { }
