import { Component, OnInit } from '@angular/core';


import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-verification',
  templateUrl: './verification.component.html',
  styleUrls: ['./verification.component.css']
})
export class VerificationComponent implements OnInit {
  loginUserData
  constructor(

    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {

    this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)

      if (params && params.status == 'yes') {
        this.service.succ('Thanks! Your email has been verified')

          if(params && params.userId){

            if(!this.loginUserData){
              this.getUserDetails(params.userId)
            }else if(this.loginUserData && this.loginUserData.id!=parseInt(params.userId)){
              this.route.navigate(['/login'])
            }else{
              this.getUserDetails(params.userId)
            }

          }
      } else if (params && params.status == 'no') {
        this.service.err('Email id is  not verified')
      }
        else {
        // this.service.err('Email not verified successfully')
      }

    });


    if (this.loginUserData && this.loginUserData.id) {
      this.getUserDetails(this.loginUserData.id)
    }
    console.log(this.loginUserData)
  }

  getUserDetails(userId?) {


  
    let id = userId
    this.service.getApi(`web/getUserDetails?userId=${id}`).subscribe((success) => {
      console.log(success)
      if (success.response == true) {
        localStorage.setItem('userTesting', JSON.stringify(success.result));
        localStorage.setItem("userTestingWebLog", 'yeppp')

        if (success.result.emailVerify == 'yes') {
          // this.service.succ('Email verified successfully')
          this.route.navigate(['/user','invite'])
        }
        //  this.service.sweetAlert('Please check your mail for updated link')

      }
      else {
        // this.service.sweetAlert(success.message)
        // this.service.err(success.message)

      }
    }, error => {
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })
    this.service.showSpinner()
  }


  onResend() {


    let apiData = {
      "userId": this.loginUserData.id,

    }

    this.service.postApi('web/resendEmailverification', apiData, 0).subscribe((success) => {
      console.log(success)
      if (success.response == true) {

        this.service.succ('Please check your mail for updated link')

      }
      else {
        this.service.err(success.message)
        // this.service.err(success.message)

      }
    }, error => {
      this.service.err("Something went wrong")
      //this.service.err("Something went wrong")

    })
    this.service.showSpinner()
  }

}
