import { Component, OnInit } from '@angular/core';


import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';

import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-project-create-details',
  templateUrl: './project-create-details.component.html',
  styleUrls: ['./project-create-details.component.css']
})
export class ProjectCreateDetailsComponent implements OnInit {

  projectFrom: FormGroup
  loginUserData
  projectId
  formInvalid = false
  instaructionError = false
  urlError = false
  selectedIndex = 0
  showDescription = ''
  roundId
  roundData
  newStatus = false

  constructor(
    private formBuilder: FormBuilder,
    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) { }


  onBack() {

    if (!this.newStatus) {
      this.route.navigate(['/user', 'project-selftest'], {
        queryParams: {
          projectId: this.projectId
        }
      })
    } else {
      this.service.err("Please add round details")
    }

    // [routerLink]="['/user','project-selftest']" [queryParams]="{projectId:projectId}"
  }
  ngOnInit(): void {

    this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)
      this.projectId = params.projectId
      if (params.roundId) {
        this.roundId = params.roundId
        this.onRoundData()
      }

      if (params.newProject) {
        debugger
        this.newStatus = true

      }
    })
    console.log(this.loginUserData)

    this.projectFrom = this.formBuilder.group({
      // orgId: new FormControl('', [Validators.required]),
      welcomeMessage: [null],
      landingPage: [null, Validators.required],
      finalMessage: [null],
      instructions: this.formBuilder.array([this.createItem()])
    });

  }
  onDismiss() {
    this.selectedIndex = 0
  }
  onRoundData() {

    // co nsole.log(this.LoginForm.value)


    this.service.getApiWithAuth(`web/findRoundDetails?roundId=${this.roundId}`).subscribe((success) => {
      console.log(success)
      if (success.response == true) {

        this.roundData = success.result
        this.projectFrom.patchValue({
          welcomeMessage: success.result.welcomeMessage,
          landingPage: success.result.landingPage,
          finalMessage: success.result.finalMessage
        });

        let instruction = []
        if (success.result.instruction) {
          instruction = JSON.parse(success.result.instruction)
          console.log(instruction)
          for (let index = 0; index < instruction.length; index++) {
            this.alternateEmail.at(index).patchValue({
              instruct: instruction[index].instruct,

            });
            if (index < instruction.length - 1) {
              this.addItem()
            }

          }

        }

      }
      else {


      }
    }, error => {
      // this.service.sweetAlert("Something went wrong")
      //this.service.err("Something went wrong")

    })


  }
  addItem() {
    console.log('ssss')
    const control = <FormArray>this.projectFrom.controls['instructions']
    control.push(this.createItem())
  }
  RemoveItem(index) {
    const control = <FormArray>this.projectFrom.controls['instructions']
    control.removeAt(index)

  }
  createItem(): FormGroup {
    return this.formBuilder.group({
      instruct: [null, Validators.required],

    })
  }

  get alternateEmail() { return this.projectFrom.get('instructions') as FormArray }

  getData() {
    const element = this.projectFrom.value.instructions[this.selectedIndex];
    return element.instruct
    // data.push(element.instruct)
  }
  onIndexChange(type) {

    if (this.selectedIndex < this.projectFrom.value.instructions.length - 1 && type == 'add') {
      this.selectedIndex = this.selectedIndex + 1
    }

    if (this.selectedIndex > 0 && type == 'min') {
      this.selectedIndex = this.selectedIndex - 1
    }
  }
  onSubmit() {
    let data = []
    this.instaructionError = false
    this.urlError=false
   let regexp =  /^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/;
  
  //  regexp.test(str)

    let url=this.getValidUrl(this.projectFrom.value.landingPage)
  let testResult= regexp.test(url)
  console.log(testResult)
  debugger
    if(!testResult){
      this.urlError=true
      return
    }
    for (let index = 0; index < this.projectFrom.value.instructions.length; index++) {
      const element = this.projectFrom.value.instructions[index];

      if (element.instruct) {
        data.push({ instruct: element.instruct })
      }


    }
    if (this.projectFrom.invalid || data.length == 0) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      if (data.length == 0) {
        this.instaructionError = true
      }
      debugger
      return

    } else {
      this.formInvalid = false
      console.log(this.projectFrom.value)
      this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
      console.log(this.loginUserData)


      let apiData = {
        "welcomeMessage": this.projectFrom.value.finalMessage,
        "landingPage": this.getValidUrl(this.projectFrom.value.landingPage),
        "instruction": JSON.stringify(data),
        "finalMessage": this.projectFrom.value.finalMessage,
        "projectId": this.projectId
      }

      if (this.roundId) {
        apiData['roundId'] = this.roundId
        this.service.postApiWithAuth('web/updateRound', apiData, 1).subscribe((success) => {
          console.log(success)
          if (success.response == true) {
            this.newStatus = false
            this.service.succ(success.message)
          }
          else {
            this.service.err(success.message)
          }
        }, error => {
          this.service.err("Something went wrong")
        })

      } else {
        this.service.postApiWithAuth('web/createRound', apiData, 1).subscribe((success) => {
          console.log(success)
          if (success.response == true) {
            this.service.succ(success.message)
            this.roundId = success.result.id
            this.newStatus = false
          }
          else {

            this.service.err(success.message)
          }
        }, error => {
          this.service.err("Something went wrong")
        })
      }

      this.service.showSpinner()
    }
  }
  isValidHttpUrl(string) {
    let url;
    
    try {
      url = new URL(string);
    } catch (_) {
      return false;  
    }
  
    return url.protocol === "http:" || url.protocol === "https:";
  }
  getValidUrl = (url) => {
    let newUrl = window.decodeURIComponent(url);
    newUrl = newUrl.trim().replace(/\s/g, "");

    if (/^(:\/\/)/.test(newUrl)) {
      return `http${newUrl}`;
    }
    if (!/^(f|ht)tps?:\/\//i.test(newUrl)) {
      return `http://${newUrl}`;
    }

    return newUrl;
  };


}

