import { Component, OnInit } from '@angular/core';


import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-project-create-round',
  templateUrl: './project-create-round.component.html',
  styleUrls: ['./project-create-round.component.css']
})
export class ProjectCreateRoundComponent implements OnInit {

  loginUserData
  projectId
  constructor(

    private route: Router,
    private service: AppService,
    private activeRoute: ActivatedRoute
  ) {

  }

  ngOnInit(): void {

    this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
    this.activeRoute.queryParams.subscribe(params => {
      console.log(params)
      this.projectId = params.projectId
    })
    console.log(this.loginUserData)
  }

  onClick() {
    this.route.navigate(['/user', `project-create-details`], { queryParams: { projectId: this.projectId, newProject: 'yes' } })

  }

}
