import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AppService } from '../../app.service';

import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-project-create',
  templateUrl: './project-create.component.html',
  styleUrls: ['./project-create.component.css']
})
export class ProjectCreateComponent implements OnInit {

  projectFrom: FormGroup
  formInvalid = false
  type = 'password'
  loginUserData = null
  constructor(
    private route: Router,
    private service: AppService,
  ) { }


  // createdBy: { type: Sequelize.INTEGER},
  // orgId: { type: Sequelize.INTEGER},
  // title: { type: Sequelize.STRING },
  // description: { type: Sequelize.STRING},
  ngOnInit(): void {
    this.projectFrom = new FormGroup({
      // orgId: new FormControl('', [Validators.required]),
      title: new FormControl('', [Validators.required, Validators.maxLength(50),]),
      description: new FormControl(''),
    });
  }
  onSubmit() {

    if (this.projectFrom.invalid) {
      this.formInvalid = true
      // this.service.sweetAlert('fghjk')

      return

    } else {
      this.formInvalid = false
      console.log(this.projectFrom.value)
      this.loginUserData = JSON.parse(localStorage.getItem('userTesting'))
      console.log(this.loginUserData)
      let apiData = {
        "title": this.projectFrom.value.title,
        "description": this.projectFrom.value.description,
        "orgId": this.loginUserData.orgDetails.id
      }

      this.service.postApiWithAuth('web/createProject', apiData, 1).subscribe((success) => {
        console.log(success)
        if (success.response == true) {


          this.route.navigate(['/user', `project-create-round`], { queryParams: { projectId: success.result.id, newProject: 'yes' } })



          //  this.service.sweetAlert('User Registration Successfull')

        }
        else {
          this.service.err(success.message)
          // this.service.sweetAlert(success.message)
          // this.service.err(success.message)

        }
      }, error => {
        this.service.err("Something went wrong")
        // this.service.sweetAlert("Something went wrong")
        //this.service.err("Something went wrong")

      })
      this.service.showSpinner()
    }
  }

}
